<template>
  <div class="businessProcess-top-style">
<!--    <div class="top-main border-style">-->
<!--      <div class="title">{{labelTit}}业务量</div>-->
<!--      <div-->
<!--        class="charts-main"-->
<!--        v-loading="!ordersBusinessLineRes"-->
<!--        element-loading-background="rgba(0, 0, 0, 0.2)"-->
<!--        element-loading-text="加载中"-->
<!--      >-->
<!--        <bda-line-charts-->
<!--          v-if="ordersBusinessLineRes"-->
<!--          :chartData="ordersBusinessLineData"-->
<!--          :nameXs="nameXs"-->
<!--        ></bda-line-charts>-->
<!--      </div>-->
<!--    </div>-->
    <div class="top-main border-style">
      <div class="title">回单及时率</div>
      <div
        class="charts-main"
        v-loading="!ordersTimeLineRes"
        element-loading-background="rgba(0, 0, 0, 0.2)"
        element-loading-text="加载中"
      >
        <bda-line-charts
          v-if="ordersTimeLineRes"
          :chartData="ordersTimeLineData"
          :xRotate="0"
          :showLegend="false"
          :yDw="dw"
        ></bda-line-charts>
      </div>
    </div>
  </div>
</template>

<script>
import Interface from '../../../base/mixin/service-index-control';
import bdaLineCharts from '../../../widgets/bda-line-charts/bda-line-charts';
//  import bda3dPie from '../../../widgets/bda-3d-pieCharts/bda-3d-pieCharts';
import qs from 'qs';

export default {
  name: "businessProcessTop",
  components: {
    bdaLineCharts
  },
  mixins: [Interface],
  data() {
    return {
      ordersBusinessLineRes: false,
      ordersBusinessLineData: {},
      ordersTimeLineRes: false,
      ordersTimeLineData: {},
      nameXs: '日',
      labelTit: '月',
      dw: '%'
    }
  },
  watch: {},
  created() {
  },
  mounted() {
    if (this.$parent.dateType=='1'){
      this.labelTit = '年'
    }else {
      this.labelTit = '月'
    }
    // 月业务量
    this.getAckOrdersBusinessForMonth(qs.stringify({
      dimensionality: this.$parent.dateType,
      timeStr: this.$parent.dateParams
    })).then(res => {
      this.ackOrdersBussinessDataFilter(res);
    }).catch(() => {
      this.ordersBusinessLineRes = true;
    });
    // 回单及时率
    this.getAckOrdersTimelinessRateForTime(qs.stringify({
      dimensionality: this.$parent.dateType,
      timeStr: this.$parent.dateParams
    })).then(res => {
      //   window.console.log('res',res)
      this.ackOrdersTimeDataFilter(res);
    }).catch(() => {
      this.ordersTimeLineRes = true;
    });
  },
  methods: {
    ackOrdersBussinessDataFilter(data) {
      let xData = [], dataMap = { '上月': [], '本月': [] };
      let status = '';
      if (data) {
        let beforMonthLen = data.beforeMonth ? data.beforeMonth.length : 0;
        let presentMonthLen = data.presentMonth ? data.presentMonth.length : 0;
        // 判读哪个数据长度较长，则作为x轴的维度信息
        if (beforMonthLen - presentMonthLen >= 0) {
          status = 'before'
        } else {
          status = 'present'
        }
        if (beforMonthLen > 0) {
          for (let item of data.beforeMonth) {
            if (item.handledate) {
              let disLength = item.handledate.length;
              let name = item.handledate.substring(disLength - 2, disLength);
              item.value = item.cc;
              item.name = name;
              if (status === 'before') {
                xData.push(name);
              }
            }
          }
        }
        if (presentMonthLen > 0) {
          for (let item of data.presentMonth) {
            if (item.handledate) {
              let disLength = item.handledate.length;
              let name = item.handledate.substring(disLength - 2, disLength);
              item.value = item.cc;
              item.name = name;
              if (status === 'present') {
                xData.push(name);
              }
            }
          }
        }
      }
      dataMap = {
        '本月': data.presentMonth,
        '上月': data.beforeMonth
      };
      this.ordersBusinessLineRes = false;
      this.ordersBusinessLineData = {
        dimension: xData,
        dataMap: dataMap,
        color: ['#019eff', '#00FFD4']

      };
      this.$nextTick(() => {
        this.ordersBusinessLineRes = true;
      })
    },
    ackOrdersTimeDataFilter(data) {
      let xData = [], dataInfo = [];
      for (let item of data) {
        dataInfo.push(item.per);
        xData.push(item.dates ? item.dates : '');
      }
      let dataMap = {
        '回单及时率': dataInfo
      };
      this.ordersTimeLineRes = false;
      this.ordersTimeLineData = {
        dimension: xData,
        dataMap: dataMap,
        color: ['#00FFD4']
      };
      this.$nextTick(() => {
        this.ordersTimeLineRes = true;
      })
    }
  }
}
</script>

<style scoped lang="scss">
.businessProcess-top-style {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;

  .top-main {
    background: url("../../../assets/img/管理框1.png") no-repeat top center;
    background-size: 104% 100%;
    // padding: 10px 20px;
    // box-sizing: border-box;
    flex: 1;
    height: 100%;
    position: relative;
    float: left;
    font-size: 16px;
    .title {
      // background: linear-gradient(
      //   to bottom,
      //   rgba(15, 146, 229, 0.3),
      //   #061b3a
      // );
      color: #00e7ff;
      // line-height: 38px;
      height: 22%;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    .charts-main {
      height: calc(100% - 22% - 40px);
      // box-sizing: border-box;
      width: calc(100% - 40px);
      position: relative;
      padding: 0 20px;
    }
  }
}
</style>